import React, { useState } from "react";
import {
  Dialog,
  IconButton,
  Box,
  Typography
} from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';
import ReactPlayer from 'react-player/lazy'

export default function Player({ open, onClose, video }) {
  // 
  const [displaySubtitles, setDisplaySubtitles] = useState(false)
  // 
  const [subtitle, setSubtitle] = useState("")
  // 
  const [translation, setTranslation] = useState("")

  // 
  const formatTime = (seconds) => {
    const hour = Math.floor(seconds / 3600);
    const min = Math.floor(seconds % 3600 / 60);
    const sec = Math.floor(seconds % 60)

    if (hour !== 0) {
      return `${hour}:${min.toString().padStart(2, '0')}:${sec.toString().padStart(2, '0')}`
    } else if (min !== 0) {
      return `${min}:${sec.toString().padStart(2, '0')}`
    } else {
      return `${sec.toString()}s`
    }
  }

  // 
  const handleOnPlay = () => {
    setDisplaySubtitles(true)
  }

  // 
  const handleOnPause = () => {
    setDisplaySubtitles(false)
  }

  // 
  const handleProgress = (state) => {
    const formattedTime = formatTime(state.playedSeconds)
    if (video?.subtitles[formattedTime]) {
      setSubtitle(video?.subtitles[formattedTime].subtitle)
      setTranslation(video?.subtitles[formattedTime].translation)
    }
  }

  // 
  const clearSubtiles = () => {
    setSubtitle("")
    setTranslation("")
    setDisplaySubtitles(false)
  }

  return (
    <Dialog
      fullScreen
      open={open}
      onClose={onClose}
      sx={{ height: "101dvh" }}
    >
      <Box sx={{ position: "relative" }}>
        {/*  */}
        <Box
          sx={{
            position: "absolute",
            top: 11,
            left: 11,
            color: "white",
            background: "rgba(0, 0, 0, 0.4)",
            borderRadius: "50%",
            zIndex: 10000
          }}
        >
          <IconButton
            color="inherit"
            onClick={() => {
              clearSubtiles()
              onClose()
            }}
          >
            <CloseIcon />
          </IconButton>
        </Box>
        <Box
          sx={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100dvh",
          }}
        >
          {/*  */}
          <ReactPlayer
            url={`https://youtu.be/${video?.id}`}
            width={"100%"}
            height={"100dvh"}
            controls={true}
            onPlay={handleOnPlay}
            onPause={handleOnPause}
            onProgress={handleProgress}
          />
          {/*  */}
          {displaySubtitles && (
            <Box
              sx={{
                position: 'absolute',
                bottom: "18svh",
                width: "100%",
                textAlign: "center",
                background: "rgba(0, 0, 0, 0.4)",
                paddingY: 0.5
              }}
            >
              <Typography
                variant="body1"
                sx={{ color: "white" }}
              >
                {subtitle}
              </Typography>
              <Typography
                variant="body2"
                sx={{
                  fontSize: "70%",
                  color: "rgb(255, 255, 255, 0.8)"
                }}
              >
                {translation}
              </Typography>
            </Box>
          )}
        </Box>
      </Box>
    </Dialog >
  );
}
