import React, { useState } from "react";
import {
    Dialog,
    Grid2,
    IconButton,
    Box,
    Stack,
    TextField,
    Button,
    Typography
} from "@mui/material";
import CloseIcon from '@mui/icons-material/Close';
import * as XLSX from "xlsx"
import { getVideoDataList } from "../utils";


function SaveVideo({ open, onClose, handleSetVideoDataList }) {
    // ID
    const [videoId, setVideoId] = useState("");
    // 
    const [videoTitle, setVideoTitle] = useState("")
    // 
    const [fileName, setFileName] = useState(null);
    // 
    const [subtitles, setSubtitles] = useState(null);

    // 
    const handleFileChange = (event) => {
        const file = event.target.files?.[0];
        if (file) {
            setFileName(file.name);
            const reader = new FileReader()
            reader.readAsArrayBuffer(file)
            reader.onload = (e) => {
                const data = new Uint8Array(e.target.result)
                const workbook = XLSX.read(data, { type: "array" })

                // 
                const sheet = workbook.Sheets[workbook.SheetNames[0]]
                const json = XLSX.utils.sheet_to_json(sheet, { header: 1 })

                // JSON
                const subtitles = {}
                json.slice(1).forEach(row => {
                    const time = row[0]?.toString().trim()
                    const subtitle = row[1]?.toString().trim()
                    const translation = row[2]?.toString().trim()

                    if (time) {
                        subtitles[time] = { subtitle, translation }
                    }
                })

                setSubtitles(subtitles)
            }
        };
    }

    // 
    const handleSave = () => {
        // 
        const subtitlesData = {
            id: videoId,
            title: videoTitle,
            subtitles: subtitles
        }

        // 
        const videoDataList = getVideoDataList()
        videoDataList.push(subtitlesData)

        // 
        window.localStorage.setItem("videoDataList", JSON.stringify(videoDataList))

        // state
        handleSetVideoDataList(videoDataList)

        // 
        clearInput()
        onClose()
    }

    // 
    const clearInput = () => {
        setVideoId("")
        setVideoTitle("")
        setFileName("")
    }

    return (
        <Dialog
            open={open}
            onClose={onClose}
            fullWidth
        >
            <Box>
                <Grid2
                    container
                    direction="row"
                    sx={{
                        justifyContent: "space-between",
                        alignItems: "center"
                    }}
                >
                    {/*  */}
                    <Typography sx={{ padding: 1 }}>Save video data</Typography>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={() => {
                            clearInput()
                            onClose()
                        }}
                        aria-label="close"
                    >
                        <CloseIcon />
                    </IconButton>
                </Grid2>
            </Box>
            <Box sx={{ paddingX: 1, paddingBottom: 1 }}>
                <Stack spacing={1}>
                    {/* videoId  */}
                    <TextField
                        label="ID"
                        variant="outlined"
                        fullWidth
                        value={videoId}
                        size="small"
                        onChange={(e) => setVideoId(e.target.value)}
                    />
                    {/* videoTitle  */}
                    <TextField
                        label="Title"
                        variant="outlined"
                        fullWidth
                        value={videoTitle}
                        size="small"
                        onChange={(e) => setVideoTitle(e.target.value)}
                    />
                    {/*  */}
                    <Box>
                        <input
                            type="file"
                            id="file-input"
                            accept=".xlsx, .xls"
                            style={{ display: "none" }}
                            onChange={handleFileChange}
                        />
                        <label htmlFor="file-input">
                            <Button
                                sx={{
                                    textTransform: "none"
                                }}
                                color="inherit"
                                variant="outlined"
                                component="span"
                                fullWidth
                            >
                                {fileName || "Select Excel file"}
                            </Button>
                        </label>
                    </Box>

                    {/*  */}
                    <Button
                        variant="contained"
                        fullWidth
                        onClick={handleSave}
                        color="inherit"
                        sx={{
                            textTransform: "none"
                        }}
                    >
                        Save
                    </Button>
                </Stack>
            </Box>
        </Dialog>
    );
}

export default SaveVideo;
